.oncoprintjs__tooltip {
  padding: 5px;
  margin-top: -10px;
  max-width: unset !important;
  min-width: 225px !important;
  word-break: keep-all; }

.nobreak {
  white-space: nowrap;
  display: inline-block; }

.oncoprint-hidden {
  opacity: 0; }

.oncoprintjs__scroll_div::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  background-color: rgba(0, 0, 0, 0);
  -webkit-border-radius: 100px; }

.oncoprintjs__scroll_div::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.21);
  -webkit-border-radius: 100px;
  width: 8px;
  height: 8px; }

.oncoprintjs__scroll_div::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.3); }

.oncoprintjs__scroll_div::-webkit-scrollbar-thumb:active {
  background: rgba(0, 0, 0, 0.42);
  -webkit-border-radius: 100px; }
